import React from 'react'
import { useState } from "react";
import OXGrid from "./XO";
import { useEffect } from "react";
import { io } from "socket.io-client";
import swal from 'sweetalert';
import { useParams } from 'react-router-dom';


function Main() {
    const { id: roomId } = useParams()
    var [socket, Setsocket] = useState()
    var [StateList, SetStateList] = useState([-1, -1, -1, -1, -1, -1, -1, -1, -1])
    var [Value, SetValue] = useState(-1)
    var [next, setNext] = useState(false)

    useEffect(() => {
        const s = io(`https://tictactoemultiplayer.onrender.com/`)
        Setsocket(s)
        return () => {
            s.disconnect()
        }
    }, [])
    useEffect(() => {
        if (socket == null || socket === undefined) return;
        console.log("Hello");
        socket.emit("connect-to-room", roomId);
        socket.on("leave", () => {
            socket.disconnect()
        })
    }, [socket, roomId])

    useEffect(() => {
        if (socket == null || socket === undefined) return;
        socket.on("Value", (val, nxt) => {
            if (val === -2) {
                swal("Room Already Occupied", "You will be redirected to Homepage...", "info").then(() => {
                    window.location.href = "https://tictactoemultiplayer.onrender.com/"
                })
            }
            SetValue(val)
            setNext(nxt)
        })
        socket.on("receive-change", (delta, nxt) => {
            SetStateList(delta)
            setNext(nxt)
        })
        socket.on("finished", (win) => {
            var popup = "Draw";
            if (win === true) {
                popup = "You Won"
            } else if (win === false) {
                popup = "You Lost"
            }
            swal(popup, "", "info").then(() => {
                SetStateList([-1, -1, -1, -1, -1, -1, -1, -1, -1])
                window.location.href = `https://tictactoemultiplayer.onrender.com/`
            })
            // if (win === true) {
            //     swal("You Won", "", "info");
            //     SetStateList([-1, -1, -1, -1, -1, -1, -1, -1, -1])
            // } else if (win === false) {
            //     swal("You lost", "", "info");
            //     SetStateList([-1, -1, -1, -1, -1, -1, -1, -1, -1])
            // } else {
            //     swal("Draw", "", "info");
            //     SetStateList([-1, -1, -1, -1, -1, -1, -1, -1, -1])
            // }
            // window.location.href = `https://tictactoemultiplayer.onrender.com/`;
        })
    }, [socket])

    if (Value === -1) {
        return (
            <div className="h-full">
                <div className="h-full py-20 h-1/2 flex justify-center">
                    <div className="w-full mx-1 md:mx-0 md:w-1/2 lg:w-1/3 bg-gray-800 rounded-lg text-white flex justify-center items-center">
                        Waiting For Someone to join The room
                    </div>
                </div>
                <div className='flex justify-center flex-wrap'>
                    <div className='m-3 bg-blue-500 rounded-lg p-3 border-box'>{roomId}</div>
                    <div className='m-3 bg-green-500 rounded-lg p-3 border-box'>ðŸ‘ˆ Share the Room Id</div>
                </div>
                <div className='flex justify-center flex-wrap'>
                    <div className='m-3 bg-red-500 rounded-lg p-3 border-box hover:cursor-pointer' onClick={() => {window.location.href = `https://tictactoemultiplayer.onrender.com/`;}}>Back</div>
                </div>
            </div>
        )
    }
    return (
        <>
            <div className='fixed top-2 left-2 p-2 rounded-lg bg-white text-white bg-gray-900'>
                <div>
                    {next === true ? "Your Chance" : "Opponent's Chance"}
                </div>
            </div>
            <div className='fixed bottom-2 right-2 p-2 rounded-lg bg-white text-white bg-gray-900 shadow-2xl text-white'>
                <div className='flex flex-col justify-center items-center '>
                    You Are
                    <img src={Value===0?'circle.svg':'cross.svg'}></img>
                </div>
            </div>
            <div>
                <div className="h-full py-20 flex justify-center align-middle">
                    <div className="w-2/3 sm:w-1/2 md:w-1/3 h-fit bg-gray-800 rounded-lg text-white">
                        <OXGrid value={Value} socket={socket} StateList={StateList} SetState={SetStateList} next={next}></OXGrid>
                    </div>
                </div>
            </div>
        </>
    );

}

export default Main;
