import React from 'react'
import {
  BrowserRouter,
  Switch,
  Route,
} from "react-router-dom";
import Home from './Home';
import Main from './Main';


function App() {
  return (
      <BrowserRouter>
        <Switch>
          <Route exact path="/">
            {/* <Redirect to={`/${uuidV4()}`}></Redirect> */}
            <Home></Home>
          </Route>
          <Route path='/:id'>
            <Main></Main>
          </Route>
        </Switch>
      </BrowserRouter>
  );

}

export default App;
