import React, { useState } from "react";
import swal from "sweetalert";
import { v4 as uuidV4 } from "uuid";

const Home = () => {
    var [room, SetRoom] = useState()
    var Handler = () => {
        const regexExp = /^[0-9a-fA-F]{8}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{12}$/gi;
        if(regexExp.test(room) === true){
            window.location.href = `https://tictactoemultiplayer.onrender.com/${room}`;
        }else{
            swal("not a valid room","","error");
        }; 
    }

    return (
        <div className="w-full h-full flex justify-center items-center">
            <div className="m-3 sm:m-0 w-full sm:w-1/3 bg-gray-900 rounded-lg h-1/2 shadow-2xl text-black flex flex-col">
                <div className="border-box h-1/2 p-3 flex justify-center items-center">
                    <a className="h-1/4 bg-blue-500 w-3/4 flex justify-center items-center rounded-lg" href={`/${uuidV4()}`}>
                        Create New Room
                    </a>
                </div>
                <div className="border-box h-1/2 p-3 flex justify-around items-center">
                    <input className="h-1/4 bg-white w-1/2 flex justify-center items-center rounded-lg" id="room"
                        onChange={(event) => { SetRoom(event.target.value) }} value={room}>
                    </input>
                    <button className="h-1/4 bg-green-500 w-1/4 flex justify-center items-center rounded-lg"
                        onClick={() => { Handler() }}>
                        join
                    </button>
                </div>
            </div>
        </div>
    )
}

export default Home;