import React from 'react'

const OXCell = (props) => {
    const handleUpdate = () => {
        if(props.next!==true) return;
        
        var newState = props.StateList.map((x) => x)
        newState[props.Id] = props.value
        props.socket.emit("send-update", newState)
    }
    const State = props.State[props.Id]
    if (State === -1) {
        return (
            <div className={"bg-gray-700 hover:bg-gray-700" + (props.next === true?"cursor-pointer":"cursor-not-allowed")} onClick={() => { handleUpdate() }}>
                <div className='hover:bg-gray-600 h-full w-full'>

                </div>
            </div>
        )
    } else if (State === 1) {
        return (
            <div className="border-box md:p-5">
                <img className="w-full aspect-auto" src="circle.svg" alt="Hello"></img>
            </div>
        )
    } else if (State === 0) {
        return (
            <div className="border-box md:p-5">
                <img className="w-full aspect-auto" src="cross.svg" alt="Hello"></img>
            </div>
        )
    }
}
const OXGrid = (props) => {
    return (
        <div className="grid gap-1 grid-cols-3 grid-rows-3 aspect-square bg-white">
            {props.StateList.map((element, index) => <OXCell next={props.next} key={index} value={props.value} Id={index} State={props.StateList} SetState={props.SetState} socket={props.socket} StateList={props.StateList}></OXCell>)}
        </div>
    )
}

export default OXGrid;